<template>
	<div
		class="w-full transition duration-1000 transform"
		:class="[isVisible ? 'translate-x-0 opacity-1' : (side ? '-translate-x-full' : 'translate-x-full') + ' opacity-0']"
	>
		<h2 class="text-2xl font-bold mb-8">{{ project.title }}</h2>
		<a :href="!getVideoLink ? project.links[0].href : null" target="_blank">
			<div
				class="mx-auto shadow-2xl rounded-xl cursor-pointer transition transform duration-300 hover:scale-105"
				@click="getVideoLink ? (showVideo = true) : null"
			>
				<img v-if="!showVideo" class="rounded-xl" :src="require('@/assets/images/' + project.img)" />
				<div v-else-if="getVideoLink" class="aspect-w-16 aspect-h-9 rounded-xl">
					<div ref="videoPlayer"></div>
					<iframe
						:src="getVideoLink"
						frameborder="0"
						class="w-full h-full"
						allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
						allowfullscreen
						seamless
					></iframe>
				</div>
			</div>
		</a>
		<div>
			<div class="flex justify-evenly px-4 my-2 w-40 mx-auto">
				<a
					v-for="(link, index) in project.links"
					:key="index"
					:href="link.href"
					target="_blank"
					class="transform duration-500 transform"
					:style="{ transitionDelay: 5 + index + '00ms' }"
					:class="[isVisible ? 'translate-y-0 opacity-1' : 'translate-y-full opacity-0']"
					><SVGIcon :name="link.icon" class="w-8 h-8 text-green-300 animate-pulse transition transform hover:scale-110"
				/></a>
			</div>
			<p class="richtext mt-6 text-lg" v-html="project.copy"></p>
			<ul class="richtext mt-6">
				<li v-for="(point, index) in project.outline" :key="index" class="my-2">
					<b>{{ index }}</b
					>: {{ point }}
				</li>
			</ul>
			<!-- <Button text="Read more" theme="secondary" glow="true" class="my-8" :href="project.href" /> -->
		</div>
	</div>
</template>

<script>
import SVGIcon from "@/components/SVGIcon";
// import Button from "@/components/Button";

export default {
	name: "ProjectCard",
	props: ["project", "side", "isVisible"],
	components: {
		// Button,
		SVGIcon,
	},
	data() {
		return {
			showVideo: false,
		};
	},
	computed: {
		getVideoLink() {
			if (this.project.links && this.project.links[0].href.indexOf("//youtu") > -1) {
				let link = this.project.links[0].href;

				link = link.replace("watch?v=", "embed/");
				link = link.replace("youtu.be/", "youtube.com/embed/");

				return link + "?autoplay=1&mute=1";
			}

			return false;
		},
	},
};
</script>
