<template>
	<div
		class="layout-segment"
		:class="{
			'flex flex-wrap flex-col sm:flex-row sm:flex-nowrap justify-evenly space-x-0 space-y-4 sm:space-x-8 sm:space-y-0':
				data.layout == 'row',
		}"
	>
		<segment
			v-for="(d, index) in data.segments"
			:key="index"
			:data="d"
			:editing="editing"
			ref="segment"
			exclude="layout"
			@reload="onReload($event, index)"
			@action="onRemove(index)"
		/>
	</div>
</template>

<script>
export default {
	name: "LayoutSegment",
	props: ["data", "editing"],
	methods: {
		onUpdate() {
			this.data.rows = this.data.rows ? this.data.rows : 2;

			if (!this.data.segments) this.data.segments = [];
			if (this.data.segments.length < this.data.rows) {
				while (this.data.segments.length < this.data.rows) this.data.segments.push({});
			} else if (this.data.segments.length > this.data.rows) {
				this.data.segments.splice(this.data.rows, 99);
			}

			this.$forceUpdate();
		},
		onReload(newData, index) {
			this.data.segments[index] = newData;

			this.$forceUpdate();
		},
		onRemove(index) {
			this.data.segments[index] = {};

			this.$forceUpdate();
		},
	},
};
</script>

<style>
.layout {
	outline-color: white;
}
</style>
