<template>
	<div class="relative w-full sm:w-3/4 mx-auto within-layout">
		<div v-if="isVideo" class="w-full aspect-w-16 aspect-h-9 h-50vw sm:h-auto">
			<iframe
				:src="data.url"
				frameborder="0"
				class="w-full h-full"
				allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
				allowfullscreen
			></iframe>
		</div>

		<img v-else-if="!isVideo" :src="isOnline ? data.url : require('@/assets/images/' + data.url)" class="mx-auto" />
		<!-- @/assets/images/project_1.jpg -->

		<div v-if="editing" class="absolute hidden-outside w-full z-10 top-0 left-0 p-2">
			<div class="bg-gray-200 w-full rounded-lg shadow-xl p-1">
				<div class="flex items-center">
					<font-awesome-icon icon="link" class="mx-2" />
					<input
						type="text"
						name="url"
						class="w-full h-full px-2 py-1 rounded-lg"
						v-model="data.url"
						@change="onUpdate"
					/>
				</div>

				<div v-if="!isVideo" class="flex items-center mt-1">
					<font-awesome-icon icon="paragraph" class="mx-2" />
					<input type="text" name="url" class="w-full h-full px-2 py-1 rounded-lg" v-model="data.alt" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: "MediaSegment",
	props: ["data", "editing"],
	data() {
		return {
			isVideo: false,
			isOnline: false,
		};
	},
	methods: {
		onUpdate() {
			this.isVideo = this.checkVideo();
			this.isOnline = this.checkOnline();
			this.$forceUpdate();
		},
		checkVideo() {
			return this.data.url.indexOf("embed") != -1;
		},
		checkOnline() {
			return this.data.url.indexOf("http") != -1;
		},
	},
	mounted() {
		this.isVideo = this.checkVideo();
		this.isOnline = this.checkOnline();
	},
};
</script>

<style>
.layout-segment .within-layout {
	@apply w-full;
}
</style>
