<template>
	<div class="px-6 md:px-16 my-12" v-intersect="intersect">
		<div class="anchor-point -top-36" id="contact" />

		<div
			class="max-w-5xl mx-auto transition duration-500 transform"
			:class="[isVisible ? 'translate-y-0 opacity-1' : 'translate-y-full opacity-0']"
		>
			<div class="flex lg:justify-center">
				<Subheading class="mb-12 inline">CONTACT</Subheading>
			</div>

			<p class="text-lg text-gray-400 mb-8">
				I'll would love to setup a meeting with you, so feel free to contact me with any questions or inquires you may
				have using the contact information below.
			</p>

			<div class="grid grid-cols-1 lg:grid-cols-2 gap-x-1 gap-y-8 text-gray-400">
				<a
					v-for="(social, index) in personalData.socials"
					:key="index"
					:href="social.link"
					target="_blank"
					class="flex transform duration-500 transform"
					:style="{ transitionDelay: 5 + index + '00ms' }"
					:class="[isVisible ? 'translate-y-0 opacity-1' : 'translate-y-full opacity-0']"
				>
					<div class="w-8 h-8">
						<SVGIcon
							:name="social.icon"
							class="w-8 h-8 text-green-300 animate-pulse transition transform hover:scale-110"
						/>
					</div>
					<p class="font-medium mx-4 underline transition duration-200 hover:text-gray-200">
						{{ cleanURI(social.link) }}
					</p>
				</a>
			</div>
		</div>
	</div>
</template>

<script>
import onIntersect from "@/mixins/onIntersect";
import Subheading from "@/components/Subheading";
import SVGIcon from "@/components/SVGIcon";

import PersonalJSON from "@/assets/data/personal.json";

export default {
	name: "Contact",
	mixins: [onIntersect],
	components: {
		Subheading,
		SVGIcon,
	},
	data() {
		return {
			personalData: PersonalJSON,
		};
	},
	methods: {
		cleanURI: function (link) {
			if (link.indexOf("mailto:") !== -1) {
				link = link.substr(7);
			}

			return link;
		},
	},
};
</script>
