<template>
	<div>
		<div
			class="fixed w-screen h-screen top-0 left-0 z-20 bg-black opacity-60"
			v-if="menuOpened"
			@click="toggleMenu(false)"
		></div>

		<div
			class="fixed z-50 flex w-screen justify-center transition-height duration-300 items-center"
			:class="[scrollY < 1 && !menuOpened ? 'h-32' : 'bg-navy-600 h-20 shadow-xl']"
			v-intersect="intersect"
		>
			<div class="w-full px-6 md:px-16 max-w-5xl h-16 flex justify-between items-center">
				<div>
					<a @click="scrollToTop()" class="cursor-pointer">
						<h2 class="text-2xl font-bold text-white whitespace-nowrap tracking-tighter">
							<span class="text-green-400 text-glow">&#60;/</span>
							{{ personalData.fullName }}
							<span class="text-green-400 text-glow">></span>
						</h2>
					</a>
				</div>
				<div class="text-xl text-gray-500 tracking-tight">
					<div class="hidden sm:flex space-x-12">
						<div
							v-for="(section, index) in sections"
							:key="index"
							class="transition duration-500 transform"
							:style="{ transitionDelay: index + 'ms' }"
							:class="[isVisible ? 'translate-y-0 opacity-1' : '-translate-y-full opacity-0']"
						>
							<a
								:href="section.link ? section.link : ''"
								v-smooth-scroll
								@click="selectOption(index)"
								class="text-gray-300 transition hover:text-gray-100 transform hover:scale-105"
							>
								{{ section.text }}
								<SVGIcon
									v-if="section.dropdown"
									name="chevron"
									class="inline w-6 h-6 text-green-300 transition-all duration-300"
									:class="{ 'transform rotate-180': menuOpened }"
								/>
							</a>

							<div
								v-if="selectedSection === index"
								class="absolute w-8 h-1 bg-green-300 rounded-xl opacity-70 mt-1"
							></div>
						</div>
					</div>

					<button
						class="
							inline-block
							sm:hidden
							w-10
							h-10
							p-2
							rounded-full
							border-glow border-2 border-green-300
							cursor-pointer
							transition
							transform
							hover:scale-105
						"
						@click="toggleMenu()"
					>
						<SVGIcon name="menu" class="w-full h-full -mt-3 text-green-300" />
					</button>
				</div>
			</div>
		</div>

		<MobileMenu
			:opened="menuOpened"
			@select="
				menuOpened = false;
				selectedSection = $event;
			"
			:sections="sections"
			:selectedSection="selectedSection"
		/>

		<MegaMenu v-if="sections[1].dropdown" :opened="menuOpened" :items="sections[1].dropdown" />
	</div>
</template>

<script>
import onIntersect from "@/mixins/onIntersect";
import SVGIcon from "@/components/SVGIcon";
import MobileMenu from "@/components/MobileMenu";
import MegaMenu from "@/components/MegaMenu";

import PersonalJSON from "@/assets/data/personal.json";
//import ProjectsJSON from "@/assets/data/projects.json";

export default {
	name: "Menu",
	mixins: [onIntersect],
	components: {
		SVGIcon,
		MobileMenu,
		MegaMenu,
	},
	data() {
		return {
			personalData: PersonalJSON,
			sections: [
				{ text: "About", link: "#about", forceHome: true },
				{ text: "Projects", link: "#projects", forceHome: true },
				{ text: "Contact", link: "#contact", forceHome: true },
			],
			scrollY: null,
			selectedSection: 0,
			menuOpened: false,
		};
	},
	methods: {
		handleScroll() {
			this.scrollY = window.scrollY || window.scrollTop;
		},
		scrollToTop() {
			if (window.location.pathname === this.$baseURL) {
				window.scrollTo({ top: 0, behavior: "smooth" });
			} else {
				window.location.href = this.$baseURL;
			}
		},
		toggleMenu(value) {
			this.menuOpened = value ? value : !this.menuOpened;

			if (this.menuOpened) {
				document.body.style.overflowY = "hidden";
			} else {
				document.body.style.overflowY = "auto";
			}
		},
		selectOption(index) {
			if (this.sections[index].forceHome && window.location.pathname !== this.$baseURL) {
				window.location.href = this.$baseURL + this.sections[index].link;
			}

			this.selectedSection = index;

			if (this.sections[index].dropdown) {
				this.toggleMenu();
			} else {
				if (this.menuOpened) {
					this.toggleMenu(false);
				}
			}
		},
	},
	mounted() {
		/** 
		this.sections[1].dropdown = [];

		for (const slug in ProjectsJSON) {
			this.sections[1].dropdown.push({
				text: ProjectsJSON[slug].title,
				link: `/projects/${slug}`,
				excerpt: ProjectsJSON[slug].copy.substring(0, 50) + "...",
				img: ProjectsJSON[slug].img,
			});
		}
    */
	},
	created() {
		window.addEventListener("scroll", this.handleScroll);
	},
	destroyed() {
		window.removeEventListener("scroll", this.handleScroll);
	},
};
</script>

<style scoped>
.text-glow {
	animation: glow 1s ease-in-out infinite alternate;
}

.bg-navy-600 {
	background-color: rgb(40, 52, 68);
}

.bg-navy-500 {
	background-color: hsl(214, 26%, 19%);
}

@keyframes glow {
	from {
		text-shadow: 2px 2px 8px rgba(52, 211, 153, 0.6);
	}
	to {
		text-shadow: 2px 2px 8px rgba(52, 211, 153, 1);
	}
}
</style>
