<template>
	<div class="group relative flex-1">
		<component
			v-if="data && data.type"
			:is="data.type + '-segment'"
			ref="segment"
			:data="data"
			:editing="editing"
		></component>

		<div v-else class="flex flex-wrap justify-center items-center text-white p-2 bb">
			<div v-for="(segment, index) in segments" :key="index">
				<button
					v-if="!exclude || segment.type.indexOf(exclude) == -1"
					@click="changeType(segment.type)"
					class="w-16 h-16 m-2 bb transform hover:scale-110"
				>
					<font-awesome-icon :icon="segment.icon" />
				</button>
			</div>
		</div>

		<div v-if="editing && data && data.type" class="absolute -right-5" :class="[isLayout ? 'top-6' : '-top-5']">
			<button
				class="w-10 h-10 bg-gray-100 rounded-full flex items-center justify-center"
				:class="{ 'opacity-0 group-hover:opacity-80': !showMenu }"
				@click="getOptions ? (showMenu = !showMenu) : $emit('action')"
			>
				<font-awesome-icon :icon="getOptions ? 'edit' : 'times'" />
			</button>

			<div
				v-if="showMenu"
				class="absolute top-0 sm:top-12 -left-52 sm:-left-16 w-48 rounded-lg bg-white p-2 shadow-inset arrow-up"
				:class="{ 'opacity-0 group-hover:opacity-100': !showMenu }"
			>
				<div v-for="(option, index) in getOptions" :key="index">
					<label :for="option.field" class="block w-full text-xs p-1 text-center">{{ capitalize(option.field) }}</label>
					<select
						:name="option.field"
						class="w-full p-1 bg-gray-200 rounded-md"
						@change="updateData(option.field, $event)"
					>
						<option
							v-for="(opt, i) in option.options"
							:key="i"
							:value="opt"
							:selected="data[option.field] && opt == data[option.field]"
						>
							{{ capitalize(opt) }}
						</option>
					</select>
				</div>

				<button
					class="w-full mt-2 mb-1 p-1 bg-gray-200 rounded-md text-center text-red-500 text-xs"
					@click="$emit('action')"
				>
					Delete
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import LayoutSegment from "@/components/Segments/LayoutSegment";
import TextSegment from "@/components/Segments/TextSegment";
import MediaSegment from "@/components/Segments/MediaSegment";

export default {
	name: "Segment",
	props: ["data", "editing", "exclude"],
	components: {
		LayoutSegment,
		TextSegment,
		MediaSegment,
	},
	data() {
		return {
			showMenu: false,
			segments: [
				{
					type: "layout",
					icon: "columns",
					options: [
						{ field: "layout", options: ["row", "grid", "carousel"] },
						{ field: "rows", options: ["2", "3", "4"] },
					],
				},
				{ type: "text", icon: "paragraph" },
				{
					type: "media",
					icon: "photo-video",
				},
			],
		};
	},
	methods: {
		onUpdate() {
			if (this.$refs.segment && this.$refs.segment.onUpdate) this.$refs.segment.onUpdate();
			this.$forceUpdate();
		},
		changeType(type) {
			let newData = {};

			switch (type) {
				case "layout":
					newData.layout = "row";
					newData.segments = [{}, {}];
					break;
				case "text":
					newData.content =
						"<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Mauris sit amet lacus eget elit finibus efficitur. Sed sollicitudin turpis lacus, vestibulum iaculis quam fringilla sed. Duis id magna semper, tempus nibh et, posuere dui. Nulla eu erat sit amet enim tempor tincidunt. Sed bibendum, elit sit amet ornare condimentum, nunc metus ornare mauris, id tincidunt lorem risus ac nunc. Donec risus nisi, pharetra quis tincidunt at, laoreet in est. Sed volutpat est ac sem dictum, et condimentum mauris luctus. Suspendisse potenti. Maecenas gravida aliquam tortor quis rutrum.</p>";
					break;
				case "media":
					newData.url = "https://www.youtube.com/embed/dQw4w9WgXcQ";
					break;
			}

			newData.type = type;
			this.$emit("reload", newData);
		},
		capitalize(str) {
			return str.charAt(0).toUpperCase() + str.slice(1);
		},
		updateData(field, e) {
			let newData = this.data ? this.data : {};
			newData[field] = e.target.value;

			this.$emit("action", newData);
		},
	},
	computed: {
		isLayout() {
			return this.data && this.data.type === "layout";
		},
		getOptions() {
			if (this.data && this.data.type) {
				for (var segment in this.segments) {
					if (this.segments[segment].type == this.data.type) return this.segments[segment].options;
				}
			}

			return null;
		},
	},
};
</script>

<style>
.shadow-inset {
	box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.6);
}

.arrow-up::before {
	content: " ";
	position: absolute;
	top: -8px;
	left: calc(50% - 20px);
	width: 0;
	height: 0;
	border-left: 10px solid transparent;
	border-right: 10px solid transparent;

	border-bottom: 8px solid rgba(255, 255, 255, 0.6);
}

@media (max-width: 640px) {
	.arrow-up::before {
		top: 12px;
		left: 100%;
		border-top: 10px solid transparent;
		border-bottom: 10px solid transparent;

		border-left: 8px solid rgba(255, 255, 255, 0.6);
		border-right: none;
	}
}
</style>
